/**
 * @description 福利列表（组件）
 * @author 顾超<beyondc@foxmail.com>
 */
// 组件
import WCard from '@/components/WCard'
import WMerchantItem from '@/components/WMerchantItem'
// 依赖
import Qs from 'qs'
import baseUrl from '@/utils/doman'
export default {
    name: 'WelfareList',
    components: {
        WCard,
        WMerchantItem
    },
    props: {
        list: {
            type: Array,
            require: true
        }
    },
    data() {
        return {
            storeItem: {
                    store_name: '',
                    address: '',
                    total_receive: '',
                    desc: '',
                    store_avatar: ''
                }
        }
    },
    computed: {
        welfareList () {
            return this.list.map(item => {
                item = item.merchant
                item.desc = `存有${item.total_receive}瓶啤酒`
                const file = {
                    filename: item.store_avatar,
                    type: 'avatar'
                }
                item.store_avatar = `${baseUrl}v1/files/download?${Qs.stringify(file)}`
                return item
            })
        }
    }
}



