/**
 * @description 福利码（组件）
 * @author 顾超<beyondc@foxmail.com>
 */
// 组件
import WCard from '@/components/WCard'
import baseUrl from '@/utils/doman'

// 依赖
import Cookies from 'js-cookie'
export default {
    name: 'WelfareQrCode',
    components: {
        WCard
    },
    data() {
        return {
            qrCode: `${baseUrl}v1/customers/qrcode?access_token=${Cookies.get(
                'Authorization'
            )}`
        }
    }
    // props: {
    //     qrCode: {
    //         type: String,
    //         required: true
    //     }
    // }
}



